<template>
  <div class="section container ticket-container">
    <div class="ticket-row is-align-items-center">
      <div class="ticket-col ticket-col__left">
        <img
          :src="artSrc"
          class="event-logo"
          :alt="artAlt"
          @error="imgError = true"
        />
      </div>
      <div class="ticket-col ticket-col__right">
        <qrcode
          class="qr-code"
          :value="ticketOffer.id"
          :options="{ width: 1200, margin: 0 }"
          tag="img"
        ></qrcode>
      </div>
    </div>
    <div class="ticket-row">
      <div class="header-text">
        <div class="admit is-size-4">
          ADMIT ONE
        </div>
        <h1 class="title is-2">
          {{ event.title }}
        </h1>
        <div class="ticket-details is-size-4">
          <div><strong>First Name:</strong> {{ person.firstName }}</div>
          <div><strong>Last Name:</strong> {{ person.lastName }}</div>
          <div><strong>Email:</strong> {{ person.email }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VueQrcode from "@chenfengyuan/vue-qrcode";
import { personNameMixin } from "@/mixins/personNameMixin";

export default {
  name: "Ticket",
  components: { qrcode: VueQrcode },
  mixins: [personNameMixin],
  data() {
    return {
      imgError: false
    };
  },
  props: {
    person: {
      type: Object,
      required: true
    },
    event: {
      type: Object,
      required: true
    },
    ticketOffer: {
      type: Object,
      required: true
    }
  },
  computed: {
    artSrc() {
      return this.imgError
        ? "/assets/ticketing-app/images/tedx-auckland-logo-black.svg"
        : `/assets/ticketing-app/images/ticket-art-${this.event.slug}.svg`;
    },
    artAlt() {
      return this.imgError ? "TEDxAuckland Ticket" : this.event.slug;
    }
  }
};
</script>

